		<path d="M0 9.06249V11.25H2.1875L8.63917 4.79833L6.45167 2.61083L0 9.06249ZM10.3308 3.10666C10.5583 2.87916 10.5583 2.51166 10.3308 2.28416L8.96583 0.91916C8.73833 0.69166 8.37083 0.69166 8.14333 0.91916L7.07583 1.98666L9.26333 4.17416L10.3308 3.10666Z" :fill="defaultColor"/>
	</svg>

</template>

<script>
export default {
	name: "pencil",

	props: {
		color: {
			default: "#D5D5D5"
		},
		hoveredColor: {
			default: "#E6442F"
		}
	},

	data() {
		return {
